<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="slds-form-element slds-grid section">
        <label class="slds-form-element__label cursor-label" @click="onToggleClick">
          Enable Charket SMS Integration
        </label>
        <div class="multiple-info">
          <!-- start -->
          <!-- refactor: adjust tooltip component to support disapear when has no message content then remove these code -->
          <div
            class="enabled-btn"
            :class="{'disabled-btn': !settings.IsCharketSmsEnabled}"
            @click="onToggleClick"
            v-if="!settings.Message"
          >
            <span class="slds-icon_container check-icon-span" v-if="settings.IsCharketSmsEnabled">
              <icon name="check" icon-class-name="slds-icon slds-icon_xx-small"></icon>
            </span>
            <div
              :class="{
                'charket-sms-enabled': hasClickedToggle && settings.IsCharketSmsEnabled,
                'is-multiple': settings.IsCharketSmsEnabled,
                'charket-sms-disabled': hasClickedToggle && !settings.IsCharketSmsEnabled
              }"
              :style="{'background-color': settings.IsEditable ? '#fff' : '#dddbda'}"
            >
            </div>
          </div>
          <!-- end -->
          <ld-tooltip class="tooltip"  v-if="settings.Message">
            <div class="tooltip-activator" slot="activator">
              <div class="enabled-btn" :class="{'disabled-btn': !settings.IsCharketSmsEnabled}" @click="onToggleClick">
                <span class="slds-icon_container check-icon-span" v-if="settings.IsCharketSmsEnabled">
                  <icon name="check" icon-class-name="slds-icon slds-icon_xx-small"></icon>
                </span>
                <div
                  :class="{
                    'charket-sms-enabled': hasClickedToggle && settings.IsCharketSmsEnabled,
                    'is-multiple': settings.IsCharketSmsEnabled,
                    'charket-sms-disabled': hasClickedToggle && !settings.IsCharketSmsEnabled
                  }"
                  :style="{'background-color': settings.IsEditable ? '#fff' : '#dddbda'}"
                >
                </div>
              </div>
            </div>
            <template>
              <p>
                {{settings.Message}}
              </p>
            </template>
          </ld-tooltip>

          <div class="enabled-info">
            {{ settings.IsCharketSmsEnabled ? 'Enabled' : 'Disabled' }}
          </div>
        </div>
      </div>
    </div>
  </right-page>
</template>
<script>
import { CharketSmsSettingsService } from '../service'
import { LdTooltip } from 'charket-ui/dist/ld-tooltip.js'

export default {
  components: {
    LdTooltip
  },
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SMS',
        name: 'Charket Sms Settings'
      },
      isSaving: false,
      hasClickedToggle: false
    }
  },
  computed: {
    smsIntegrationSettings () {
      return this.$store.state.charketSms.smsIntegrationSettings
    },
    settings () {
      return this.smsIntegrationSettings || {}
    }
  },
  async mounted () {
    if (typeof this.smsIntegrationSettings === 'undefined') {
      this.$bus.emit('show-loading')
      const watcher = this.$watch('smsIntegrationSettings', () => {
        this.$bus.emit('hide-loading')
        watcher()
      })
    } else {
      this.$bus.emit('hide-loading')
    }
  },
  methods: {
    onToggleClick () {
      if (this.isSaving || !this.smsIntegrationSettings.IsEditable) {
        return
      }
      this.hasClickedToggle = true
      this.$store.commit('charketSms/SET_SMS_ENABLED', !this.smsIntegrationSettings.IsCharketSmsEnabled)
      this.saveCharketSmsSettings()
    },
    async saveCharketSmsSettings () {
      try {
        this.isSaving = true
        await CharketSmsSettingsService.saveCharketSmsSettings(this.smsIntegrationSettings)
      } catch (error) {
        this.$store.commit('charket-sms/SET_SMS_ENABLED', !this.smsIntegrationSettings.IsCharketSmsEnabled)
        this.$bus.emit('error', { content: error.message, type: 'error' })
      } finally {
        this.isSaving = false
      }
    }
  }
}
</script>
