  padding: 20px 30px;
  .cursor-label {
    cursor: pointer;
  }
  .multiple-info {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    .enabled-btn {
      padding-top: 8px;
      width: 48px;
      height: 24px;
      background-color: #0070D2;
      border-radius: 12px;
      position: relative;
      cursor: pointer;
      .check-icon-span {
        position: absolute;
        left: 6px;
        top: 4px;
      }
      div {
        width: 20px;
        height: 20px;
        border-radius: 10px;
        background: #fff;
        position: absolute;
        top: 2px;
        left: 2px;
        cursor: pointer;
      }
      .is-multiple {
        left: 26px;
      }
    }
    .enabled-info {
      font-size: 10px;
      color: #706E6B;
    }
    .disabled-btn {
      background-color: #7e8c99;
    }
    .charket-sms-enabled {
      animation: bounce-in .2s linear;
      animation-fill-mode: forwards;
    }
    @keyframes bounce-in {
      0% {
        left: 2px;
      }
      50% {
        left: 14px;
      }
      100% {
        left: 26px;
      }
    }

    .charket-sms-disabled {
      animation: bounce-out .2s linear;
      animation-fill-mode: forwards;
    }
    @keyframes bounce-out {
      0% {
        left: 26px;
      }
      50% {
        left: 14px;
      }
      100% {
        left: 2px;
      }
    }
  }
}
</style>
